<template>
    <div class="header" id="header">
        <div class="g-tb">
            <div class="m-tb">
                <ul class="u-tb u-tbl">
                    <li class="u-li"></li>
                </ul>
                <ul class="u-tb u-tbr">
                    <li class="u-li u-sp u-name"><a href="#"><span>{{userName}} </span></a></li>
                    <li class="u-li u-sp"  v-if="!this.$cookie.get('user_name')">
                        <el-button type="text" @click="checkCookie"></el-button>
                        <el-dialog title="" v-model="FormVisible" size="tiny">
                            <el-form :model="form">
                                <el-form-item label=" " :label-width="formLabelWidth">
                                    <el-input v-model="form.name" placeholder="" auto-complete="off"></el-input>
                                </el-form-item>
                                <el-form-item label=" " :label-width="formLabelWidth">
                                    <el-input type="password" placeholder="" v-model="form.password"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="FormVisible = false"> </el-button>
                                <el-button type="primary" @click="login" :disabled= btndsiabled> </el-button>
                            </div>
                        </el-dialog>
                    </li>
                    <li class="u-li"  v-if="!this.$cookie.get('user_name')">
                        <el-button type="text" @click="dialogFormVisible = true"></el-button>
                        <el-dialog title="" v-model="dialogFormVisible" size="tiny">
                            <el-form :model="form">
                                <el-form-item label=" " :label-width="formLabelWidth">
                                    <el-input v-model="form.name" placeholder="" auto-complete="off"></el-input>
                                </el-form-item>
                                <el-form-item label=" " :label-width="formLabelWidth">
                                    <el-input type="password" placeholder="616
                                    " v-model="form.password"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="dialogFormVisible = false"> </el-button>
                                <el-button type="primary" @click="register" :disabled= btndsiabled > </el-button>
                            </div>
                        </el-dialog>
                    </li>
                    <li class="u-li u-sp u-name" v-if="this.$cookie.get('user_name')"><a @click="unLogin"><span>  </span></a></li>
                </ul>
            </div>
        </div>
        <div class="g-hd">
            <div class="m-lg">
                <router-link :to="{path:'/'}">
                    <span class="u-logo"></span>
                    <h1 class="u-title"></h1>
                </router-link>
            </div>
            <div class="m-nav">
                <ul class="u-nav">
                    <li><router-link :to="{path:'/'}" ><span class="nav-item"></span></router-link</li>
                    <li>
                        <router-link :to="{path:'/products'}"><span class="nav-item"></span></router-link>
                    </li>
                    <li>
                        <router-link :to="{path:'/news'}"><span class="nav-item"></span></router-link>
                    </li>
                    <li>
                        <router-link :to="{path:'/active'}"><span class="nav-item"></span></router-link>
                    </li>
                    <li>
                        <router-link :to="{path:'/aboutus'}"><span class="nav-item"></span></router-link>
                    </li>
                </ul>
            </div>
            <div class="sear-cart">
                <span class="search"><img src="../../assets/img/searc.png" alt=""></span>
                <span class="cart"><img src="../../assets/img/cart.png" alt="">
                    <i class="cart-num" @click="showCart">{{cartNumber}}</i>
                </span>
                <div class="cart-list" v-if="showcart">
                   <el-card class="cart-card clearfix" v-for="(o,index) in cart" :key="o.cart_id">
                        <img class="cart-img" :src=o.commodity_pic alt="o.commodity_name">
                        <span class="c-name">{{o.commodity_name}}</span>
                        <span class="c-num">{{o.number}}</span>
                        <span class="c-total">{{Number(o.number) * Number(o.price)}}</span>
                        <div class="cart-btn">
                            <el-button type="warning" @click='deleteCart(index)'></el-button>
                        </div>
                    </el-card>
                </div>
            </div>
        </div>
    </div>
</template>

<script> 
    import axios from 'axios'
	export default{
		name:'MainNav',
		data(){
			return {
				dialogFormVisible: false,
                FormVisible : false,
                btndsiabled : false,
                form: {
                    name: '',
                    password:'',
                },
                formLabelWidth: '100px',
                userName:this.$cookie.get('user_name'),
                cart:null,
                showcart:false,
                total:null
			}
		},
        created(){
            return this.getCartInfo();
        },
        methods: {
            register() {
                this.btndsiabled = true;
                if(this.form.name && this.form.password){
                    const pattern = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/g
                    if(pattern.test(this.form.name)){
                        axios.post('http://www.lthuali.com/api/register',{name:this.form.name,password:this.form.password}).then((response)=>{
                            if(response.data.status){
                                this.$notify({
                                    title: '',
                                    message: '',
                                    type: 'success',
                                });
                                this.btndsiabled = false;
                                this.form.name = null;
                                this.form.password = null;
                                setTimeout(()=>{
                                    this.dialogFormVisible = false;
                                },1000);
                            }else{
                                this.$notify.error({
                                    title: '',
                                    message: response.data.msg
                                });
                                this.btndsiabled = false;
                            }
                        });
                    }else{
                        this.$notify({
                            title: '',
                            message: '',
                            type: 'warning'
                        });
                        this.btndsiabled = false;
                    }
                }else{
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                    this.btndsiabled = false;
                }
            },
            login(){
                if(this.$cookie.get('user_name')){
                    this.FormVisible = false;
                    return this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                }
                if( !(this.form.name && this.form.password)){
                    return this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                }
                this.btndsiabled = true;
                axios.post('http://www.lthuali.com/api/login',{name:this.form.name,password:this.form.password}).then((response)=>{
                    if(response.data.status){
                        this.$notify({
                            title: '',
                            message: response.data.msg,
                            type: 'success',
                        });
                        this.$cookie.set('user_name', this.form.name , 1);
                        this.btndsiabled = false;
                        this.form.name = null;
                        this.form.password = null;
                        setTimeout(()=>{
                            this.FormVisible = false;
                            location.reload();
                        },1000);
                    }else{
                        this.$notify.error({
                            title: '',
                            message: response.data.msg,
                        });
                        this.btndsiabled = false;
                    }
                });
            },
            checkCookie(){
                if(this.$cookie.get('user_name')){
                    this.FormVisible = false;
                    return this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                }else{
                    this.FormVisible = true;
                }
            },
            getCartInfo(){
                let username = this.$cookie.get('user_name');
                if(username){
                    this.$ajax.get('http://www.lthuali.com/api/cart/' + username).then((response)=>{
                        this.cart = response.data
                    })
                }
            },
            showCart(){
                if(!this.$cookie.get('user_name')){
                    return this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                }else if(this.cart.length === 0){
                    return this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                }
                return this.showcart = !this.showcart;
            },
            deleteCart(index){
                let cartid = this.cart[index].cart_id;
                this.$ajax.delete('http://www.lthuali.com/api/delete?cart_id=' +  cartid).then((response)=>{
                    if(response.data.status){
                        this.$message({
                            message: response.data.msg,
                            type: 'success'
                        });
                        this.cart.splice(index,1);
                    }else{
                        this.$message({
                            message: response.data.msg,
                            type: 'warning'
                        });
                    }
                })
            },
            unLogin(){
                this.$cookie.delete('user_name');
                location.reload();
            }   
        },
        computed:{
            cartNumber(){
                return this.cart ? this.cart.length : 0; 
            }
        }
	}
</script>

<style scoped>


</style>



